    .containerCards{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 15em;
    }
    .containerCards-grid{
        display: grid;
        grid-template-rows: 20% 60% 20%;
        max-width: 100%;
        height: 45em;
    }
    .title_cards{
        display: flex;
        justify-content: center;
    }
    .title_cards div h2{
        display: flex;
        justify-content: space-around;
        margin: 0px;
        font-size: 40px;
        color: rgb(46, 45, 45);
    }
    .buttonAndlabel{
        display: grid;
        grid-template-rows: 50% 50%;
        height: 120px;
        width: 160px;
    }
    .cotainerButtonAndlabel_{
        display: flex;
        justify-content: center;
    }
    .cards_Container{
        display: flex;
        justify-content: center;
    }
    .cards_{
        display: flex;
        justify-content: center;
        height: 140px;
        width:40em;
        display: grid;
        grid-template-columns:50% 50%;
    }
    .buttonAndlabel label{
        font-size: 14px;
        display: flex;
        justify-content: center;
        color: rgb(147, 145, 145);
        margin-top: 20px;
    }
    .cards_ .itemCard{
        background-color: rgb(255, 255, 255);
        border-radius: 5px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        -ms-border-radius: 5px;
        -o-border-radius: 5px;
        border: 1px solid rgb(219, 217, 217);
        display: grid;
        grid-template-rows: 33%;
        margin: 10px;
    }
    .cards_ .itemCard:hover{
        cursor: pointer;
        margin:0px
    }
    .itemCard_{
        display: flex;
        justify-content: center;
    }
    .itemCard_ div h2{
        display:flex;
        justify-content: center;
        align-items: center;
    }

    

